import React, { Component } from 'react';
import classes from './Dropdown.css';


class Dropdown extends Component {
    // constructor() {
    //     super();
    //     this.state = {
    //         display: false,
    //     };
    // }

    // toggleDropdown = (e) => {
    //     // e.preventDefault();
    //     console.log('clicked');
    //     this.setState(prevState => ({
    //         display: !prevState.display
    //     }));
    // }

    render() {
        const skillList = this.props.skillList.map((skill,index)=>(
            <li key={skill+index}>{skill}</li>
        ))
        return (
            <div className={classes.Dropdown}>
                <div className={classes.drpButton}>{this.props.skillName}</div>
                <ul>
                    {skillList}
                </ul>
            </div>
        )
    }

}

export default Dropdown;