import React from 'react';
import Title from '../Title/Title';
import classes from './About.css';
const about = () => {
    return (
        <div className={classes.About}>
            <Title pname="Welcome"/>
            <p style={{ fontFamily: "'Raleway',sans-serif" }}>Hi there,</p>
            <div className={classes.style}>
                <p>
                    I am a graduate of the University of Wollongong
                    and completed my Bachelor of Computer Science on Decemeber 2018, with my
                    majors being in Software Engineering and Big Data.
                </p>
                <p>
                    I am currently seeking oppurtunities to work and gain experience, so that I
                    can utilize my skills and learn a lot in the process. I am driven and ambitious and always try to have an optimistic outlook even in dire situations.
                </p>
                <p>Some of my hobbies and interests are:</p>
                <ul>
                    <li>Solving coding challenges online to find the optimal solutions. </li>
                    <li>Participating in hackathons. </li>
                    <li>Physical activities such as hiking, soccer and basketball </li>
                </ul>
                <p>
                    Thank you for taking the time to visit my page. If there is any information you require please contact me.
                </p>

            </div>
            <p style={{ fontFamily: "'Raleway', sans-serif" }}>Kind Regards,<br/>David Lalrinawma</p>


        </div>
    );
}


export default about;