import React from 'react';
import Title from '../Title/Title';
import Dropdown from '../../UI/Dropdown/Dropdown';
import  classes from './Skills.css';

const skills = {
    "Software Development": ["C++", "SQL", "C#"],
    "Web Development": ["React", "Node.js", "Javascript", "Html/Css"],
    "Big Data": ["HDFS", "python"],
    "Basic level skills": ["Hive", "Pig", "HBase", "MongoDB", "Java", "Software Design"]
};
const experience = () => {
    const displayDropDown = Object.keys(skills).map((key,index)=>(
        <Dropdown key={key+index}skillName={key} skillList={skills[key]} />
    ));
    return (<div className={classes.pad}>
        <Title pname="Skills" />
        <div className={classes.Skills}>
            {displayDropDown}
        </div>

    </div>
    )
};


export default experience;