import React from 'react';
import classes from './Header.css';
import Self from '../../assets/self.jpeg';

const header =()=>(
    <header className={classes.Header}>
        <img className={classes.Image} src={Self} alt="my-pic" />
        <hr width='70%'/>
        <p className={classes.Name}>David Lalrinawma</p>
        <div className={classes.Info}>
            <p>University of Wollongong</p>
            <p className={classes.Degree}>Softwtare Engineering | Big Data</p>
        </div>
    </header>
)

export default header;