import React from 'react';
import classes from './Navigation.css';
import { NavLink } from 'react-router-dom';

const navigation = (props) => {
    return (
        <nav className={classes.Navigation}>
            <ul>
                <li>
                    <NavLink
                        to="/"
                        exact
                        activeStyle={classes.active}>About</NavLink></li>
                <li>
                    <NavLink
                        to="/projects"
                        activeStyle={classes.active}>Projects</NavLink></li>
                <li>
                    <NavLink
                        to="/skills"
                        activeStyle={classes.active}>Skills</NavLink></li>
                <li>
                    <NavLink
                        to="/contact"
                        activeStyle={classes.active}>Contact</NavLink></li>

            </ul>
        </nav>
    )
};


export default navigation;