import React, { Component } from 'react';
import classes from './Contact.css';
import Title from '../Title/Title';
import {CopyToClipboard} from 'react-copy-to-clipboard';

// import { Map, GoogleApiWrapper,Marker } from 'google-maps-react';

class contact extends Component {
    state={
        copied:false
    }
    render() {
        return (
            <div style={{padding:'10px'}}>
                <Title pname="Contact" />
                <div className={classes.Contact}>
                    <div className={classes.Info}>
                        <p><strong>David Lalrinawma</strong></p>
                        <p>email: <span style={{ color: "blue" }}>davidlalrin@outlook.com</span>
                        <CopyToClipboard text="davidlalrin@outlook.com"
                        onCopy={()=> this.setState({copied:true})}>
                            <button style={{borderRadius:'10px', marginLeft:'5px',marginRight:'5px', outline:'none'}}>Copy email</button>
                            
                        </CopyToClipboard>
                        {this.state.copied?<span className={classes.Copied}>Copied</span>:null}

                        </p>
                        <br />
                        <p><strong>Preffered Cities</strong></p>
                        <ul>
                            <li>Melbourne</li>
                            <li>Sydney</li>
                            <li>Brisbane</li>
                            <li>Adelaide</li>
                        </ul>

                    </div>
                    {/* <div className={classes.Map}>
                    <Map
                        google={this.props.google}
                        zoom={2}
                        style={{ width: "400px", height: "300px" }}
                        initialCenter={{ lat: 144.9631, lng: 144.9631 }}
                    >
                        <Marker position={{ lat: 144.9631, lng: 144.9631}} />
                    </Map>
                    </div> */}
                </div>
            </div>
        );
    }
}


// export default GoogleApiWrapper({ apiKey: 'AIzaSyAGGEakBrrRhjoI4D_M6fpOF7KG9KttyI0' })(contact);
export default contact;